<template>
  <div class="home">
    <el-carousel type="card" :height="height">
      <el-carousel-item v-for="item in imgList" :key="item">
        <img :src="item" alt="" />
      </el-carousel-item>
    </el-carousel>

    <el-collapse v-model="activeName" accordion>
      <el-collapse-item
        :title="e.title"
        :name="e.id"
        v-for="(e, i) of list"
        :key="i"
      >
        <div style="text-align: left; padding-left: 8px">{{ e.body }}</div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  data() {
    return {
      activeName: "1",
      imgList: [
        require("../assets/1.jpg"),
        require("../assets/2.jpg"),
        require("../assets/3.jpg"),
        require("../assets/4.jpg"),
        require("../assets/5.jpg"),
        require("../assets/6.jpg"),
      ],
      height: "",
      list: [],
    };
  },
  created() {
    this.height = window.outerHeight - 200 + "px";
    this.aj.get("http://jsonplaceholder.typicode.com/posts").then((res) => {
      console.log(res);
      if (res.status === 200) {
        this.list = res.data;
      } else {
        this.list = [];
      }
    });
  },
};
</script>
<style>
.el-carousel {
  width: 920px;
  margin: 0 auto;
}
img {
  width: 100%;
}
body {
  background: #585858;
}
</style>
